
.containerMy {
    height: 100vh;
	width: calc(48 * 4.3pt);
    margin: 0 auto;
	
}

.rnData {
    display: flex;
    justify-content: space-between;
}

.znChangeReceipt {
    display: flex;
    justify-content: space-between;
    
}

.changeReceipt{
    display: grid;
    grid-template-columns: repeat(2, auto);
    gap: 4pt;
    text-align: right;
}

.price {
    text-align: end;
}

.outPrice .outcome, .oPrice {
    font-size: 14pt;
    margin: 1px 0;
}

.formPay {
    margin-left: 4.3pt;
    text-transform: uppercase;
}

.img {
    display: block;
    width: 120px;
    margin: 5px auto 0 auto;
}
.nameCompany{
    text-transform: uppercase;
}